<template>
  <ul>
    <li class="selector about" @click="activeTab= 'aboutV', fontabout='bold',fontedu='',fontex='',fontres='',fontextra='',fontachiev=''">About</li>
    <li class="selector education" @click="activeTab= 'educationV',fontabout='',fontedu='bold',fontex='',fontres='',fontextra='',fontachiev=''">Education</li>
    <li class="selector experience" @click="activeTab= 'experienceV',fontabout='',fontedu='',fontex='bold',fontres='',fontextra='',fontachiev=''">Experience</li>
    <li class="selector research" @click="activeTab= 'researchWorksV',fontabout='',fontedu='',fontex='',fontres='bold',fontextra='',fontachiev=''">Research Works</li>
    <li class="selector extra" @click="activeTab= 'extraAcV',fontabout='',fontedu='',fontex='',fontres='',fontextra='bold',fontachiev=''">Extracurricular Activities</li>
    <li class="selector achiev" @click="activeTab= 'achievementsV',fontabout='',fontedu='',fontex='',fontres='',fontextra='',fontachiev='bold'">Achievements</li>
  </ul>
  <hr>
  <component :is="activeTab"/>

</template>

<script>
import aboutV from './about.vue';
import educationV from './education.vue';
import experienceV from './experience.vue';
import researchWorksV from './researchworks.vue';
import extraAcV from './extra.vue'
import achievementsV from './achievements.vue';
import { ref } from 'vue';

export default {
  name: 'navBar',
  components:{
    aboutV,
    educationV,
    experienceV,
    researchWorksV,
    extraAcV,
    achievementsV
  },
  setup(){
    const activeTab = ref('aboutV')
    const fontabout=ref('')
    const fontedu=ref('')
    const fontex=ref('')
    const fontres=ref('')
    const fontextra=ref('')
    const fontachiev=ref('')
    return{
        activeTab,
        fontabout,
        fontedu,fontex,fontres,fontextra,fontachiev
    }
  }
};
</script>

<style scoped>
ul{
    list-style-type: none;
    text-align: center;
    font-family: Calibri;
    margin: 0px;
    color: #212529;
    position: relative;
}
li {
  display: inline-block;
  cursor: pointer;
  font-size: 12px;
  text-decoration: none;
  outline: none;
  padding: 5px;
  font-size: 17px;
  margin-left: 10px;
  margin-right: 10px;
  position: relative;
}
.about{
